<template>
  <tr
    draggable="true"
    style="cursor: pointer"
    @dragstart="handleDragStart"
    @dragend="$emit('dragend')"
    @mousedown="tmu = 1"
  >
    <td>{{code.code}}</td>
    <td>{{code.pick}}</td>
    <td>{{code.pnd}}</td>
    <td>{{code.position}}</td>
    <td>{{code.tmu1}}</td>
    <td @mousedown.stop="tmu = 2">{{code.tmu2}}</td>
    <td @mousedown.stop="tmu = 3">{{code.tmu3}}</td>
  </tr>
</template>

<script>
export default {
  name: 'CodeDialogItemWrapper',

  props: {
    code: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      tmu: 1
    }
  },

  methods: {
    handleDragStart (event) {
      event.dataTransfer.setData('code', this.code.code + this.tmu)
      this.$emit('dragstart')
    }
  }
}
</script>
