<template>
  <tr
    :class="klass"
    :draggable="isDraggable"
    @drop="handleDrop"
    @dragstart="handleDratstart"
    @dragover="handleDragover"
    @dragenter="handleDragenter"
    @dragleave="handleDragleave"
    @dragend="handleDragend"
    @mouseover="isHovered = true"
    @mouseout="isHovered = false"
  >
    <EditableCell
      v-for="cell in primaryCells" :key="cell.prop"
      class="is-centered"
      :value="$props[cell.prop]"
      @input="handleChange({
        prop: cell.prop,
        value: $event
      })"
    />
    <td
      class="is-centered"
      style="cursor: pointer"
      @mouseup="isDraggable = false"
      @mousedown="isDraggable = true"
    >{{description}}</td>
    <td class="is-centered">{{time}}</td>
    <td
      class="is-centered"
      @click="$emit('remove', {
        operatorIndex,
        codeType,
        codeIndex
      })"
    >
      <a :class="'icon has-text-' + (isHovered ? 'danger' : 'white')">
        <strong>
          <i class="iconfont icon-close"></i>
        </strong>
      </a>
    </td>
  </tr>
</template>

<script>
import EditableCell from './EditableCell'

export default {
  name: 'TaskOperatorCodeRow',

  components: {
    EditableCell
  },

  props: {
    operatorIndex: {
      type: Number,
      required: true
    },

    codeType: {
      type: String,
      required: true
    },

    codeIndex: {
      type: Number,
      required: true
    },

    total: {
      type: Number,
      required: true
    },

    codeLib: {
      type: Object,
      required: true
    },

    code_and_distance: {
      type: String,
      required: true
    },

    frequence: {
      type: [Number, String],
      required: true
    },

    divider: {
      type: [Number, String],
      required: true
    },

    type: {
      type: [Number, String],
      required: true
    }
  },

  data () {
    return {
      isHovered: false,
      isDragging: false,
      isDraggable: false,
      isDragentering: false,
      primaryCells: [
        { prop: 'code_and_distance' },
        { prop: 'frequence' },
        { prop: 'divider' },
        { prop: 'type' },
      ]
    }
  },

  computed: {
    klass () {
      return [
        'animated',
        'code-item',
        (this.isDragentering || this.isDragging) && 'has-text-grey-lighter'
      ]
    },

    code () {
      return this.code_and_distance.substring(0, 2)
    },

    distance () {
      const distance = this.code_and_distance[2]
      if (
        !isNaN(parseInt(distance)) &&
        distance >= 1 && distance <= 3
      ) return distance

      return false
    },

    matchedCode () {
      return this.codeLib[this.code]
    },

    description () {
      if (!this.matchedCode) return ''

      const code = this.matchedCode
      let res = 'Pick Pnd Position; '
      res += code.pick + '; ' + code.pnd + '; ' + code.position

      if (this.distance) {
        res += '; distance: ' + this.distance
      }

      return res
    },

    time () {
      if (this.matchedCode && this.distance) {
        return this.matchedCode['tmu' + this.distance]
      }

      return ''
    },

    index () {
      return {
        operatorIndex: this.operatorIndex,
        codeType: this.codeType,
        codeIndex: this.codeIndex
      }
    }
  },

  methods: {
    handleChange ({prop, value}) {
      const { operatorIndex, codeType, codeIndex } = this

      this.$emit('update', {
        prop, value,
        operatorIndex,
        codeType,
        codeIndex,
      })
    },

    handleDratstart (event) {
      this.isDragging = true
      event.dataTransfer.setData('index', JSON.stringify(this.index))
    },

    handleDragover (event) {
      event.preventDefault()
    },

    handleDragenter (event) {
      this.isDragentering = true
    },

    handleDragleave (event) {
      this.isDragentering = false
    },

    handleDragend () {
      this.isDragging = false
      this.isDraggable = false
    },

    handleDrop (event) {
      const index = event.dataTransfer.getData('index')

      if (index) {
        // handle swap
        const a = JSON.parse(index)
        const b = this.index

        this.$emit('swap', a, b)
        this.isDragentering = false
      } else {
        // handle drop
        const code = event.dataTransfer.getData('code')

        this.handleChange({
          value: code,
          prop: 'code_and_distance'
        })
      }

      this.isDragentering = false
    }
  }
}
</script>
