<template>
  <div
    class="full-container is-flex-auto is-flex is-flex-column"
    :class="currentTab === id ? 'is-flex' : 'is-hidden'"
  >
    <CodeDialog
      :codes="codes"
      :isCodeDialogShow="state.isCodeDialogShow"
      @open="state.isCodeDialogShow = true"
      @close="state.isCodeDialogShow = false"
    />

    <TaskTopbar
      :mode="mode"
      :isCodeLibShow="state.isCodeLibShow"
      :isSettingsShow="state.isSettingsShow"
      :isCodeDialogShow="state.isCodeDialogShow"
      @save="$wait(handleSave)"
      @create="$wait(handleCreate)"
      @codelib="state.isCodeLibShow = !state.isCodeLibShow"
      @settings="state.isSettingsShow = !state.isSettingsShow"
      @code-dialog="state.isCodeDialogShow = !state.isCodeDialogShow"
    />

    <div class="is-flex-auto is-flex">
      <TaskPanel :codes="codes" :isCodeLibShow="state.isCodeLibShow"/>

      <div class="is-flex-auto">
        <div class="is-flex full-container">
          <Loader v-if="isLoading"/>

          <template v-else>
            <div class="is-flex-auto task-code-container">
              <TaskOperator
                v-for="(operator, operatorIndex) in task.operators"
                :key="operatorIndex"
                v-bind="operator"
                :index="operatorIndex"
                :codeLib="codes.data"
              >
                <TaskOperatorName
                  slot="header"
                  :index="operatorIndex"
                  :name="operator.name"
                  @add="handleOperatorAdd"
                  @remove="handleOperatorRemove"
                />
                <template v-for="(codeType, codeTypeIndex) in codeTypeList">
                  <TaskOperatorCodeType
                    slot="body"
                    v-bind="codeType"
                    :key="codeTypeIndex + 'head'"
                    :operator-index="operatorIndex"
                    :code-type="codeType"
                    @add="handleOperatorCodeAdd"
                  />
                  <tbody slot="body" name="fade" :key="codeTypeIndex + 'body'">
                    <TaskOperatorCodeRow
                      v-for="(code, codeIndex) in operator.codes[codeType.prop]"
                      :key="codeIndex + '' + codeTypeIndex"
                      v-bind="code"
                      :codeLib="codes.data"
                      :operator-index="operatorIndex"
                      :code-type="codeType.prop"
                      :code-index="codeIndex"
                      :total="operator.codes[codeType.prop].length"
                      @remove="handleOperatorCodeRemove"
                      @update="handleOperatorCodeUpdate"
                      @swap="handleOperatorCodeSwap"
                    />
                  </tbody>
                </template>
              </TaskOperator>
              <div style="min-width: 1px"></div>
            </div>
          </template>
        </div>
      </div>

      <TaskPanelDescription
        slot="description"
        :mode="mode"
        :isSettingsShow="state.isSettingsShow"
        v-bind="task"
        @save="$wait(handleSave)"
        @create="$wait(handleCreate)"
        @update="handleUpdateTask"
      />
    </div>
  </div>
</template>

<script>
import axios from "@/packages/axios"
import cloneDeep from "lodash/cloneDeep"

import CodeDialog from "./CodeDialog"

import TaskTopbar from "./Topbar"
import TaskPanel from "./TaskPanel"
import TaskPanelDescription from "./RightFields"
import TaskOperator from "./TaskOperator"
import TaskOperatorName from "./TaskOperatorName"
import TaskOperatorCodeRow from "./TaskOperatorCodeRow"
import TaskOperatorCodeType from "./TaskOperatorCodeType"

export default {
  name: "TaskDetail",

  components: {
    CodeDialog,
    TaskTopbar,
    TaskPanel,
    TaskPanelDescription,
    TaskOperator,
    TaskOperatorName,
    TaskOperatorCodeRow,
    TaskOperatorCodeType
  },

  props: {
    id: {
      required: true
    },

    currentTab: {
      required: true
    },

    item: {
      type: Object
    },

    codes: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      isLoading: false,
      state: {
        isSettingsShow: false,
        isCodeLibShow: false,
        isCodeDialogShow: false
      },

      task: {
        id: -1,
        description: "",
        plant: "",
        area: "",
        cost_center: "",
        type: "Single Station",
        welding_time: 0,
        turning_time: 0,
        setup_time: 0,
        operators: [this.initOperator(1)]
      },

      codeTypeList: [
        { prop: "out_door", text: "Out Door" },
        { prop: "wait_process", text: "Wait Process" },
        { prop: "in_door", text: "In Door" },
        { prop: "wait_till_end", text: "Wait Till End" }
      ]
    };
  },

  computed: {
    mode() {
      return this.id === -1 ? "create" : "update";
    }
  },

  methods: {
    getTaskById(id) {
      let handleResponse = response => {
        this.task = response.data;
      };

      return axios.post("tasks/" + id).then(handleResponse);
    },

    initCodeItem() {
      return {
        id: 0,
        code_and_distance: "",
        frequence: "",
        divider: "",
        type: ""
      };
    },

    initOperator(num = 1) {
      const initCodeBlock = size => {
        return Array.apply(null, Array(size)).map(() => this.initCodeItem());
      };

      return {
        name: "Operator " + num,
        codes: {
          in_door: initCodeBlock(3),
          wait_process: initCodeBlock(3),
          out_door: initCodeBlock(3),
          wait_till_end: initCodeBlock(1)
        }
      };
    },

    handleOperatorAdd(key) {
      const len = this.task.operators.length;

      if (len === 1) {
        this.task.operators.push(this.initOperator(len + 1));
      }
    },

    handleOperatorRemove(key) {
      this.task.operators.splice(key, 1);
    },

    handleOperatorCodeAdd({ operatorIndex, codeType }) {
      this.task.operators[operatorIndex].codes[codeType].push(
        this.initCodeItem()
      );
    },

    handleOperatorCodeRemove({ operatorIndex, codeType, codeIndex }) {
      const codes = this.task.operators[operatorIndex].codes[codeType];
      if (codes.length > 1) {
        codes.splice(codeIndex, 1);
      }
    },

    handleOperatorCodeUpdate({
      operatorIndex,
      codeType,
      codeIndex,
      prop,
      value
    }) {
      this.task.operators[operatorIndex].codes[codeType][codeIndex][
        prop
      ] = value;
    },

    handleOperatorCodeSwap(a, b) {
      const temp = this.task.operators[b.operatorIndex].codes[b.codeType][
        b.codeIndex
      ];

      this.$set(
        this.task.operators[b.operatorIndex].codes[b.codeType],
        b.codeIndex,
        this.task.operators[a.operatorIndex].codes[a.codeType][a.codeIndex]
      );

      this.$set(
        this.task.operators[a.operatorIndex].codes[a.codeType],
        a.codeIndex,
        temp
      );
    },

    handleUpdateTask({ prop, value }) {
      this.task[prop] = value;
    },

    handleSave() {
      const params = {
        id: this.task.id,
        data: this.task
      };

      const callback = response => {
        this.$emit("save", cloneDeep(this.task));
        this.$message.success("success");
      };

      return axios
        .post("tasks/update", params)
        .then(callback)
        .catch(error => console.log(error));
    },

    handleCreate() {
      const params = { data: this.task };
      const callback = response => {
        const { id } = response.data;

        this.task.id = id;

        // todo
        this.task.created_at = "";

        this.$emit("add", this.task);
        // this.$message.success('success')
      };

      return axios
        .post("tasks/create", params)
        .then(callback)
        .catch(error => console.log(error));
    }
  },

  async created() {
    this.isLoading = true;

    if (this.id !== -1) {
      await this.getTaskById(this.id)
    }

    this.isLoading = false;
    this.$emit("mounted", this.task)
  },

  mounted() {
    let { description } = this.task

    if (this.mode === "create") {
      description = "New Task"
    } else if (description == "") {
      description = "No description"
    }
  }
}
</script>

<style>
.task-code-container {
  display: flex;
  flex-flow: row;
  overflow: auto;
  padding: 0.5rem;
  padding-left: 0.25rem;
  margin-left: 0.25rem;
}
</style>
