<template>
  <div
    class="modal"
    style="z-index: 99999"
    :class="isCodeDialogShow && 'is-active'"
  >
    <div
      class="modal-background"
      @click="$emit('close')"
    ></div>
    <div class="modal-card" style="width: 1000px; height: 745px">
      <header class="modal-card-head">
        <p class="modal-card-title">Codes</p>
        <button
          class="delete"
          @click="$emit('close')"
        ></button>
      </header>
      <section class="modal-card-body">
        <table class="table is-narrow is-fullwidth is-bordered is-nowrapped is-centered is-hoverable">
          <thead>
            <th colspan="4"></th>
            <th colspan="3">TMU</th>
          </thead>
          <thead>
            <th>Code</th>
            <th>Pick</th>
            <th>Pnd</th>
            <th>Position</th>
            <th>1</th>
            <th>2</th>
            <th>3</th>
          </thead>
          <tbody>
            <CodeItem
              v-for="code in currentCodes"
              :key="code.id"
              :code="code"
              @dragstart="$emit('close')"
              @dragend="$emit('open')"
            />
          </tbody>
        </table>
      </section>
      <footer
        class="modal-card-foot is-flex"
        style="justify-content: center"
      >
        <Pagination
          :page="page"
          :total="codes.list.length"
          :perPage="perPage"
          @change-page="page = $event"
        />
      </footer>
    </div>
  </div>
</template>

<script>
import CodeItem from './CodeDialogItem'
import Pagination from '@/components/Pagination'

export default {
  name: 'CodeDialog',

  components: {
    CodeItem,
    Pagination
  },

  props: {
    codes: {
      required: true
    },

    isCodeDialogShow: {
      type: Boolean,
      required: true
    }
  },

  data () {
    return {
      page: 1,
      perPage: 15
    }
  },

  computed: {
    codesGroup () {
      const res = []
      const size = this.perPage
      const { list, data } = this.codes

      for (let i = 0, L = list.length; i < L; i += size) {
        res.push(list.slice(i, i + size)
          .map(id => data[id]))
      }

      return res
    },

    currentCodes () {
      return this.codesGroup[this.page - 1]
    }
  }
}
</script>
